.homeBestSeller{max-width:320px;padding:30px 20px}.homeBestSeller__inner{width:100%;margin:0 auto}.homeBestSeller__title{color:#032c41;font-weight:700;font-size:16px;margin-bottom:30px}.homeBestSeller__content{width:100%}.homeBestSeller__contentCont{width:100%;display:grid;grid-template-columns:repeat(1,1fr);gap:30px}@media (min-width:320px){.homeBestSeller{max-width:480px}.homeBestSeller__contentCont{display:grid;grid-template-columns:repeat(2,1fr);gap:20px}}@media (min-width:480px){.homeBestSeller{max-width:720px}.homeBestSeller__contentCont{gap:30px}}@media (min-width:720px){.homeBestSeller{max-width:860px;padding:30px 50px}.homeBestSeller__contentCont{gap:30px 60px}.homeBestSeller__title{font-size:20px}}@media (min-width:860px){.homeBestSeller{max-width:1080px}.homeBestSeller__contentCont{grid-template-columns:repeat(3,1fr)}}@media (min-width:1080px){.homeBestSeller{max-width:1280px}.homeBestSeller__contentCont{grid-template-columns:repeat(4,1fr)}}@media (min-width:1280px){.homeBestSeller{max-width:1440px}.homeBestSeller__inner{max-width:1000px}.homeBestSeller__contentCont{gap:45px}}@media (min-width:1440px){.homeBestSeller{max-width:100%}.homeBestSeller__inner{max-width:1220px}}